#ClassCarousel {
height: 100%;
}


.ClassItemCarousel {
    height: 100%;
    }
    
    .carousel-inner{
        height: 100%;
    
    }


    .carousel-inner > .item {
        height: 100%;
     }

     .carousel-inner .item img {
        height: 100%;
        background-color: rgba(0,0,0,0.6);
        
     }

     .carousel-caption{
     
       position: absolute;
       left: 40%;
       top: 50%;
       transform: translate(-50%, -50%);
       border: 5px solid #FFFF00;
       padding: 10px;
      
      }



      .carousel-caption h1{
        font-size: 6vh; 
       }

       .carousel-caption h3{
        font-size: 3vh; 
       }


     .d-block w-100{
        background-image: "../../Imagenes/Picture3.jpg";
        background-color: rgba(0,0,0,0.6);
     }

